export const getBumperPrice = async () => {
  const { LpContract } = ctx.data;
  const instance = await LpContract.at(WBNB2BUMPER);
  let res = await instance.getReserves() // token0是wbnb，token1是bumper，所以token0Amt/token1Amt就是price
  ctx.data.bumperPrice = Math.round(res._reserve0 * 100 / res._reserve1) / 100
  console.log('bumperPrice', ctx.data.bumperPrice)
  return
}
