/**
 * 页面事件代理
 */

import Config from '../config';
import ctx from './index';
import { initChain, updateAccount, updateSupply, updatePrice, updateApy } from './contracts/accounts';
import { initContract, approveCol, depositAndSwapToPaired, lend, approvePaired, repay, deposit, mmDeposit, redeem } from './contracts/transaction';
import { approveLP, stake, withdraw, claim } from './contracts/farmTransaction';
import { setAsyncData } from '../utils/cache';

// 合约地址
const { BumperCore, BumperZap, SwapRouter } = Config.CONTRACT_ADDRESS

ctx.event.listen('showLoading', (message) => {
  ctx.data.showLoading = true;
  ctx.data.loadingMessage = message;
});

ctx.event.listen('hideLoading', (message) => {
  ctx.data.showLoading = false;
  ctx.data.loadingMessage = '';
});

// 初始化并连接账户
ctx.event.listen('initEthereum', async () => {
  ctx.event.emit('showLoading', 'LOADING');
  await initChain(); // 选择网络
  await initContract(); // 选择合约
  await updateSupply(); // 更新总量信息
  await updatePrice(); // 更新price
  const { chainProvider } = ctx.data;
  if (chainProvider) {
    const account = await chainProvider.request({ method: 'eth_accounts' });
    console.log(account, 'account')
    await updateAccount(account);
    ctx.event.emit('hideLoading');
  }
  await updateApy(); // 更新apy
});

// bumper
ctx.event.listen('connectWallet', async () => {
  const { chainProvider } = ctx.data;
  if (chainProvider) {
    let account = '';
    try {
      account = await chainProvider.request({ method: 'eth_requestAccounts' });
      ctx.event.emit('showLoading', 'LOADING');
      await updateAccount(account);
      ctx.event.emit('hideLoading');
    } catch (err) {
      if (err.code === 4001) {
        // EIP-1193 userRejectedRequest error
        // If this happens, the user rejected the connection request.
        console.log('Please connect to MetaMask.');
      } else {
        console.error(err);
      }
    }
  }
});

// borrow
ctx.event.listen('depositAndSwapToPaired', async (params) => {
  const { num, token } = params;
  // 先approve
  if (ctx.data.approveBtcbInZapStatus !== true) {
    ctx.event.emit('showLoading', 'APPROVING');
    const result = await approveCol(num, BumperZap);
    ctx.data.approveBtcbInZapStatus = true;
    if (!(result && result.receipt.status)) {
      return;
    }
    ctx.event.emit('hideLoading');
  } else {
    ctx.event.emit('showLoading', 'DEPOSITING');
    const result = await depositAndSwapToPaired(num)
    console.log('depositAndSwapToPaired', result)
    ctx.event.emit('hideLoading');
  }
})

