import Config from '../../config';
import ctx from '../index';

const REWARD_PER_DAY = Config.REWARD_PER_DAY

export const getBtcbApy = async () => {
  const { stakeLp, lpTotalSupply, bcPrice, bumperPrice } = ctx.data;
  const RewardReceived = Math.round(100 * REWARD_PER_DAY * stakeLp/ lpTotalSupply) / 100;
  const RewardValue = RewardReceived * bumperPrice;
  const lpValue = (bcPrice+1) * stakeLp; // lp的价格应该等于bctoken价格+paired价格，等于+1基准
  const apy = (RewardValue / lpValue) * 365;
  ctx.data.btcbApy = Math.round(apy * 100) /100;
  // console.log('apy',  RewardValue, lpValue, RewardValue/lpValue, apy)
  return
}
