import Config from '../../config';
import ctx from '../index';
import { convertByWei,convertByEth } from '../../utils';

const { LP, BumperFarm, BUMPER } = Config.CONTRACT_ADDRESS;

// bumper函数
// 获取bumper的数目
export const getBumperAmount = async (address) => {
  const { Erc20Contract } = ctx.data;
  const instance = await Erc20Contract.at(BUMPER);
  let res = await instance.balanceOf(address)
  let amount = convertByWei(res)
  console.log('bumper', amount)
  ctx.data.BumperAmt = amount
  return
}

export const getLpAmount = async (address) => {
  const { Erc20Contract } = ctx.data;
  const instance = await Erc20Contract.at(LP);
  let res = await instance.balanceOf(address)
  let amount = convertByWei(res)
  console.log('lp', amount)
  ctx.data.lpAmt = amount
  return
}

export const getStakeLpAmount = async (address) => {
  const { BumperFarmContract } = ctx.data;
  const instance = await BumperFarmContract.at(BumperFarm);
  let res = await instance.getUser(LP,address)
  let stakeLp = res[0][0]
  let reward = res[1].length ? res[1][0] : '0'
  ctx.data.stakeLp = convertByWei(stakeLp)
  ctx.data.reward = convertByWei(reward)
  console.log('stakeLp', ctx.data.stakeLp, 'reward', ctx.data.reward)
  return
}

export const getLpTotalSupply = async () => {
  const { Erc20Contract } = ctx.data;
  const instance = await Erc20Contract.at(LP);
  let res = await instance.totalSupply()
  let amount = convertByWei(res)
  console.log('lp totalSupply', amount)
  ctx.data.lpTotalSupply = amount
  return
}

export const getLpReserves = async () => {
  const { LpContract } = ctx.data;
  const instance = await LpContract.at(LP);
  let res = await instance.getReserves()
  // let amount0 = convertByWei(res._reserve0) // _reserve0, bctoken
  let amount1 = convertByWei(res._reserve1) // _reserve1, wbnb
  ctx.data.reservePaired = 0.1 * amount1
  // console.log('lp reserve', amount1, ctx.data.reservePaired)
  return
}

// 授权lp
export const approveLP = async (amount) => {
  const { Erc20Contract, chainAccount } = ctx.data;
  const lpIns = await Erc20Contract.at(LP);
  // 授权
  try {
    let res = await lpIns.approve(
      BumperFarm,
      convertByEth(amount),
      {
        from: chainAccount
      }
    );
    return res;
  } catch (err) {
    console.log(err);
  }
};

export const isApproveLP = async () => {
  const { Erc20Contract, chainAccount } = ctx.data;
  const lpIns = await Erc20Contract.at(LP);
  const res = await lpIns.allowance(chainAccount, BumperFarm);
  let approveNum = convertByWei(res)
  console.log(approveNum, 'approveLpNum')
  if (approveNum > 0) {
    ctx.data.stakeStatus = true;
  } else {
    ctx.data.stakeStatus = false;
  }
};

// stake
export const stake = async (num) => {
  const amount = convertByEth(num)
  const { BumperFarmContract, chainAccount } = ctx.data;
  const farmIns = await BumperFarmContract.at(BumperFarm)
  try {
    const params = [
      LP,
      amount,
    ]
    let res = await farmIns.deposit(
      ...params,
      {
        from: chainAccount
      }
    );
    // 更新数据
    await getLpAmount(chainAccount);
    await getStakeLpAmount(chainAccount);
    return res;
  } catch (err) {
    //alert(err.message);
  }
}

// withdraw
export const withdraw = async (num) => {
  const amount = convertByEth(num)
  const { BumperFarmContract, chainAccount } = ctx.data;
  const farmIns = await BumperFarmContract.at(BumperFarm)
  try {
    const params = [
      LP,
      amount,
    ]
    let res = await farmIns.withdraw(
      ...params,
      {
        from: chainAccount
      }
    );
    // 更新数据
    await getLpAmount(chainAccount);
    await getStakeLpAmount(chainAccount);
    return res;
  } catch (err) {
    //alert(err.message);
  }
}

